/**
 *  Animates content section to fade in and upwards on down scroll
 *
 * @module paint-layouts/case-study-transition-effects/content-section
 * @author Walter Reyneke <walter.reyneke@city.ac.uk>
 * @copyright City, University of London 2019!
 */

import { isMobile } from '../../../util';

const className = 'parallax';

function initParallax(parallax){
    console.log('parallax');

    alert(window.navigator.userAgent);

    if (window.navigator.userAgent.indexOf('iPad') > 0 || window.navigator.userAgent.indexOf('iPhone') > 0 ) {
        console.log(`IOS detected`);
       alert('IOS detected V2');

        window.addEventListener('load', function(){
            imageDimensions();
        });

        function imageDimensions(){
            var imageSrc = parallax
                        .style
                        .backgroundImage
                        .replace(/url\((['"])?(.*?)\1\)/gi, '$2')
                            .split(',')[0];
        
            var image = new Image();
            image.src = imageSrc;

            var width = image.width,
                height = image.height;
            
            scroll(height);
        }
    

        function scroll(backgroundImageHeight){
            window.addEventListener('scroll', function(){
                let positionOnScreen = window.pageYOffset;
                let viewPortHeight = window.innerHeight;

                let parallaxHeight = parallax.offsetHeight;
                let parallaxStart = parallax.offsetTop;
                let headerHeight = document.getElementsByClassName('global-header')[0].offsetHeight + document.getElementsByClassName('breadcrumbs')[0].offsetHeight
                
                var startZone = parallaxStart + headerHeight - viewPortHeight;
                let endZone = parallaxStart + parallaxHeight + headerHeight;
            
                if(positionOnScreen > startZone && positionOnScreen < endZone){
                    let winYoffset = window.pageYOffset;
                    parallax.style.backgroundPositionY = ((parallaxHeight-backgroundImageHeight)/2)+winYoffset + "px"; 
                };
            });
        }
    }

    else{
        alert(`fixed V2`);
        parallax.style.backgroundAttachment = "fixed";
        parallax.style.backgroundSize = "cover";
    }
    
    
        
};

export default {
    launchFn: initParallax,
    launchQuery: `.${className}`,
};



